<template>
  <v-card>
    <v-toolbar
      flat
      dense
      dark
      color="primary"
    >
      <v-toolbar-title>{{ order.id }}</v-toolbar-title>
      <v-spacer/>
      <v-toolbar-items>
        <v-btn
          x-large
          color="warning"
        >
          {{ order.totalPrice }}
        </v-btn>
        <slot name="title"/>
      </v-toolbar-items>
    </v-toolbar>
    <address-info-block :address-info="addressInfo"/>
    <v-divider/>
    <dish-info-block
      :render-list="true"
      :dish-orders="order.details"
    />
    <div class="pa-2 d-flex">
      <div>Zeit:{{ order.updateTimestamp }}</div>
      <v-spacer/>
      <div>
        <slot name="information"/>
      </div>
    </div>

    <v-divider/>
    <v-card-actions>
      <v-spacer/>
      <slot name="function"/>
    </v-card-actions>
  </v-card>
</template>

<script>
  import AddressInfoBlock from '@/components/base/AddressInfoBlock'
  import DishInfoBlock from '@/components/base/DishInfoBlock'

  const DefaultAddressInfo = {
    reason: '',
    addressLine1: '',
    addressLine2: '',
    city: '',
    plz: '',
    tel: '',
    email: '',
    lastName: '',
    firstName: '',
    date: '',
    time: '',
    note: '',
    deliveryMethod: '',
  }
  export default {
    name: 'TakeawayOrderDetailCard',
    components: {
      DishInfoBlock,
      AddressInfoBlock,
    },
    props: {
      order: {
        default: () => {
        },
        type: Object,
      },
    },
    computed: {
      addressInfo () {
        if (this.order.rawAddressInfo !== null) {
          let rawAddressInfoStr = this.order.rawAddressInfo.replace(/\\/g, '-')
          rawAddressInfoStr = rawAddressInfoStr.replace(/\n/ig, '')
          return Object.assign({}, DefaultAddressInfo, JSON.parse(rawAddressInfoStr))
        } else {
          return Object.assign({}, DefaultAddressInfo, JSON.parse(this.order.rawAddressInfo))
        }
      },
    },
  }
</script>

