import GlobalSettings from '../components/Settings/GlobalSettings'

import { hillo, IKDataEntity } from 'innerken-utils'
import Payment from './Payment'
import ConsumeTypeStatus from './ConsumeTypeStatus'
import PaymentColors from './PaymentColors'
const entity = {
  orderId: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      disableNew: true,
      disableEdit: true,
      requiredNew: false,
    },
  },
  tableName: {},
  updateTimestamp: {
    displayName: '结账时间',
  },
  totalPrice: {},
  payMethod: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Payment.getList,
        itemText: '_langsname',
        color: PaymentColors.getList(),
      },
    },
    displayName: '付款状态',
  },
  consumeTypeStatusId: {
    displayName: 'consumeTypeStatus',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeTypeStatus.getList,
        itemText: 'name',
      },
      merge: false,
    },
  },
  paymentStatus: {
    displayName: 'paymentStatus',
    type: IKDataEntity.Types.Boolean,
    formConfig: {
      merge: false,
    },
  },
  guestName: {
    displayName: 'staffName',
  },
  Tel: {
    displayName: 'telephone',
  },
  Email: {
  },
  addressStr: {
    displayName: 'adress1',
  },
  LieferZeit: {
  },
}
const config = {
  load: async function (filter) {
    return (await hillo.get('Orders.php', Object.assign({
      op: 'withSortAndFilter',
      lang: GlobalSettings.lang(),
    }, ...filter)))
      .content.filter(i => i.consumeTypeId === '2').map(function (item) {
        if (item.payLogs) {
          item.paymentStr = []
          item.paymentArr = []
          item.payLogs.forEach(n => {
            if (n.paymentMethodId !== '9') {
              const paymentStr = n.name + ':' + n.amount
              item.paymentStr.push(paymentStr)
              item.paymentArr.push(n.paymentMethodId)
            }
          })
          item.paymentStr = item.paymentStr.join(' / ')
          item.paymentArr = String(item.paymentArr)
        }
        if (item.rawAddressInfo && item.rawAddressInfo !== 'null') {
          item.rawAddressInfo = item.rawAddressInfo.replace(/\\/g, '-')
          item.rawAddressInfo = item.rawAddressInfo.replace(/\n/ig, '')
          const addressInfo = JSON.parse(item.rawAddressInfo)
          item.addressStr = addressInfo.addressLine1 + ', ' + addressInfo.plz
          item.guestName = addressInfo.firstName + ' ' + addressInfo.lastName
          item.Tel = addressInfo.tel
          item.Email = addressInfo.email
          item.LieferZeit = addressInfo.reason
        } else {
          item.addressStr = ''
        }
        return item
      })
  },
}
export default IKDataEntity.ModelFactory(entity, config)
