<template>
  <v-container
    id="dashboard"
    fluid
    tag="section"
  >
    <v-container fluid>
      <v-row>
        <v-col
          v-for="order in takeawayOrders"
          :key="order.id"
          sm="12"
          md="6"
          lg="3"
        >
          <takeaway-order-detail-card
            :order="order"
          >
            <template v-slot:title>
              <v-btn
                small
                @click="goNext(order)"
              >
                <v-icon>
                  mdi-dots-vertical
                </v-icon>
              </v-btn>
            </template>
            <template v-slot:function>
              <template v-if="parseInt(order.consumeTypeStatusId)<2">
                <v-btn
                  small
                  color="red"
                  text-color="white"
                  @click="rejectOrder(order)"
                >
                  <v-icon
                    small
                  >
                    mdi-close-circle
                  </v-icon>
                </v-btn>
                <template
                  v-for="(time) in [0,30,60]"
                >
                  <v-btn
                    :key="time"
                    class="ma-1"
                    small
                    color="green"
                    text-color="white"
                    @click="acceptOrder(order,time)"
                  >
                    + {{ time }}
                  </v-btn>
                </template>
              </template>
              <template/>
            </template>
          </takeaway-order-detail-card>
        </v-col>
      </v-row>
    </v-container>
    <v-container fluid>
      <v-row>
        <v-col
          cols="12"
        >
          <ik-data-table
            ref="billTable"
            :use-default-action="false"
            :model="model"
            :filter="filter"
            entity-name="Alle Bestellungen"
            :use-select="false"
            :icon="'mdi-format-list-bulleted'"
          >
            <template v-slot:filterLeft>
              <v-menu>
                <template v-slot:activator="{ attrs, on }">
                  <v-btn
                    v-bind="attrs"
                    small
                    color="primary"
                    dark
                    v-on="on"
                  >
                    Datum wählen
                  </v-btn>
                </template>
                <v-list
                  :tile="false"
                  nav
                >
                  <v-list-item
                    v-for="(n, i) in billSelectionList"
                    :key="i"
                    @click="setBillDates(i)"
                  >
                    <v-list-item-title
                      v-text="n"
                    />
                  </v-list-item>
                </v-list>
              </v-menu>
              <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                :return-value.sync="dates"
                offset-y
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="dates"
                    class="ml-auto"
                    hide-details
                    :label="$t('日期筛选')"
                    prepend-icon="mdi-calendar"
                    readonly
                    style="max-width: 250px;"
                    single-line
                    append-icon="mdi-close"
                    @click:append="clear"
                    v-on="on"
                  />
                </template>
                <v-date-picker
                  v-model="dates"
                  no-title
                  range
                  scrollable
                  locale="de"
                >
                  <v-spacer/>
                  <v-btn
                    text
                    color="primary"
                    @click="menu = false"
                  >
                    {{ $t('Cancel') }}
                  </v-btn>
                  <v-btn
                    text
                    color="primary"
                    @click="$refs.menu.save(dates)"
                  >
                    {{ $t('OK') }}
                  </v-btn>
                </v-date-picker>
              </v-menu>
            </template>
            <template v-slot:item.action="{ item }">
              <v-icon
                class="mr-2"
                small
                @click="goDetail(item)"
              >
                mdi-arrow-right
              </v-icon>
            </template>
          </ik-data-table>
        </v-col>
      </v-row>
    </v-container>
  </v-container>
</template>

<script>
  import {
    getAllPayment,
    getConsumeTypeStatusList,
    getIndexData,
    getRestaurantInfo,
    getTakeawayOrder,
    ShowTodayBills,
  } from '@/components/Utlis/api'
  import { hillo, IKUtils, IkDataTable } from 'innerken-utils'
  import { formatDate } from '@/common/common'
  import i18n from '@/i18n'
  import moment from 'moment'
  import http from '@/components/Utlis/http'
  import TakeawayOrderDetailCard from '@/components/InK/TakeawayOrderDetailCard'
  import HistoryBillModel from '../../model/TakeawayList'
  import Utils from '../../components/Utlis/Utils'
  import { PrinterAllSummary } from '../../components/Utlis/api'

  export default {
    name: 'DashboardDashboard',
    components: { TakeawayOrderDetailCard, IkDataTable },
    data () {
      return {
        model: HistoryBillModel,
        timer2: null,
        takeawayOrders: [],
        payMethods: [],
        consumeTypeStatusList: [],
        loading: null,
        dates: [],
        menu: false,
        currentOrderId: null,
        showDetail: null,
        billSelectionList: [
          this.$i18n.t('当日结单'),
          this.$i18n.t('本周结单'),
          this.$i18n.t('本月结单'),
          this.$i18n.t('本年结单'),
        ],
        billSelectionColorList: ['green darken-2', 'blue darken-2', 'purple darken-2', 'pink'],
      }
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { timespan: this.dates ? this.dates.join(' - ') : '' }
      },
    },
    async created () {
      this.dates = [formatDate(0), formatDate(0)]
      this.onceData()
      this.reloadData()
      getAllPayment().then(res => {
        this.payMethods = res.content
        this.formatItem()
      })
      getConsumeTypeStatusList().then(res => {
        this.consumeTypeStatusList = res.content
      })
      this.reloadItem()
      this.timer2 = setInterval(this.reloadItem, 5000)
    },
    methods: {
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          PrinterAllSummary(this.filter).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
      setBillDates (i) {
        switch (i) {
          case 0:
            this.dates = [formatDate(0), formatDate(0)]
            break
          case 1:
            this.dates = [moment().startOf('week').format('YYYY-MM-DD'), formatDate(0)]
            break
          case 2:
            this.dates = [moment().startOf('month').format('YYYY-MM-DD'), formatDate(0)]
            break
          case 3:
            this.dates = [moment().startOf('year').format('YYYY-MM-DD'), formatDate(0)]
        }
      },
      clear () {
        this.dates = [formatDate(0), formatDate(0)]
      },
      calculateTotalPrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
          , 0) : 0
      },
      calculateTotalPaymentPrice (items) {
        if (!this.$refs.billTable || !this.$refs.billTable.filterItem.paymentArr || (this.$refs.billTable.filterItem.paymentArr.length === 0)) {
          return items ? items.reduce(
            (arr, item) =>
              item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
            , 0) : 0
        } else {
          const paymentArr = this.$refs.billTable.filterItem.paymentArr
          let totalMoney = 0
          paymentArr.forEach(indexItem => {
            for (const item of items) {
              if (item.payLogs !== null) {
                item.payLogs.forEach(payItem => {
                  if (parseInt(payItem.paymentMethodId) === parseInt(indexItem)) {
                    totalMoney += parseFloat(payItem.amount)
                  }
                })
              }
            }
          })
          return totalMoney
        }
      },
      findLangInArray (langArray, target = i18n.locale) {
        return langArray.find((item) => {
          return item.lang.toLowerCase() === target.toLowerCase()
        })
      },
      formatItem () {
        for (const i of this.payMethods) {
          i.name = this.findLangInArray(i.langs).name
        }
      },
      statusText: function (item) {
        if (parseInt(item.consumeTypeStatusId) === 1) {
          if (['1', '2', '3'].indexOf(item.payMethod) !== -1) {
            return 'Unbezahlt'
          } else {
            return 'Bezahlt'
          }
        } else {
          return this.consumeTypeStatusList.find(i => item.consumeTypeStatusId === i.id).name
        }
      },
      acceptOrder (item, time) {
        const addressInfo = JSON.parse(item.rawAddressInfo)
        let timeReal = moment()
        if (addressInfo) {
          if (addressInfo.date && addressInfo.time) {
            const timeRealStr = addressInfo.date + ' ' + addressInfo.time
            timeReal = moment(timeRealStr)
          }
        }
        timeReal.add(time, 'm')
        return http.post('Orders.php?op=acceptTakeawayOrder', {
          tableId: item.tableId,
          reason: timeReal.format('DD-MM-YYYY HH:mm'),
        })
          .then(response => {
            this.reloadItem()
          })
      },
      rejectOrder (item) {
        hillo.fastSweetAlertRequest(this.$i18n.t('请输入拒绝接单的理由'), 'text', 'Orders.php?op=rejectTakeAwayOrder', 'reason', { tableId: item.tableId }, this.reloadItem)
      },
      reloadItem () {
        getTakeawayOrder().then(res => {
          this.takeawayOrders = res.content
          this.takeawayOrders.forEach(takeawayOrderItem => {
            takeawayOrderItem.details = takeawayOrderItem.details.map((item, index) => ({
              id: index,
              agNameArr: item.hasMod > 0 ? item.agName.split(',') : [],
              aNameArr: item.hasMod > 0 ? item.aName.split(',') : [],
              ...item,
            }))
          })
          this.loading = false
        })
      },
      goNext (item) {
        console.log(item, 'item')
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.id },
        })
      },
      goDetail (item) {
        console.log(item, 'item')
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
      checkStorno () {
        this.stornoDialog = true
      },
      changeDateToDay () {
        this.dishIncomeList = this.indexData.thisDayIncomePieChart
      },
      changeDateToMonth () {
        this.dishIncomeList = this.indexData.thisMonthIncomePieChart
      },
      async reprint (item) {
        await this.model.reprint(item)
        IKUtils.toast()
      },
      toZbon () {
        this.$router.push({ path: '/Bills/zbonlist' })
      },
      toAllBills () {
        this.$router.push({ path: '/Bills/history-bills' })
      },
      addDishes () {
        this.$router.push({ path: '/dishes/dishes-manage' })
      },
      addCategories () {
        this.$router.push({ path: '/dishes/labels-manage' })
      },
      dishStatistic () {
        this.$router.push({ path: '/dishes/dish-statistics' })
      },
      dailyBill () {
        this.$router.push({ path: '/Bills/today-bills' })
      },
      onceData () {
        getRestaurantInfo().then(res => {
          let arr = []
          arr = res.content[0].name.split('<BR>')
          this.restaurantName = arr.join(' ')
        })
      },
      calculateAmount (Array) {
        this.cashAmount = 0
        this.cardAmount = 0
        this.creditCardAmount = 0
        Array.forEach(item => {
          if (item.payLogs !== null) {
            item.payLogs.forEach(payItem => {
              if (payItem.paymentMethodId === '2') {
                this.cardAmount += parseFloat(payItem.amount)
              }
              if (payItem.paymentMethodId === '1') {
                this.cashAmount += parseFloat(payItem.amount)
              }
              if (payItem.paymentMethodId === '3') {
                this.creditCardAmount += parseFloat(payItem.amount)
              }
            })
          } else {
            if (item.payMethod === '3') {
              this.creditCardAmount += parseFloat(item.totalPrice)
            }
            if (item.payMethod === '2') {
              this.cardAmount += parseFloat(item.totalPrice)
            }
            if (item.payMethod === '1') {
              this.cashAmount += parseFloat(item.totalPrice)
            }
          }
        })
      },
      async reloadData () {
        await getIndexData().then(res => {
          this.indexData = res
          this.dishIncomeList = this.indexData.thisMonthIncomePieChart
        })
        const timeSpan = [formatDate(0), formatDate(0)].join(' - ')
        await ShowTodayBills(timeSpan).then(res => {
          this.dishIncomeWithPLog = res.content
          this.calculateAmount(this.dishIncomeWithPLog)
        })
      },
      beforeDestroy () {
        clearTimeout(this.timer1)
        clearTimeout(this.timer2)
        this.timer1 = null
        this.timer2 = null
      },
      complete (index) {
        this.list[index] = !this.list[index]
      },
    },
  }
</script>

<style lang="sass">
  #coloured-line
    .ct-series-a .ct-line,
    .ct-series-a .ct-point
      stroke: #00cae3 !important

    #multiple-bar
      .ct-series-a .ct-bar
        stroke: #00cae3 !important

      .ct-series-b .ct-bar
        stroke: #f44336 !important

    #pie
      .ct-series-a .ct-slice-pie
        fill: #00cae3 !important

      .ct-series-b .ct-slice-pie
        fill: #f44336 !important
</style>
